.c-checkbox-expandable__group {
    padding: 0;
    margin: 0;

    border: none;
}

.c-checkbox-expandable__title {
    position: relative;

    display: block;
    padding: 0;
    padding-right: 10px;
    margin: 0;

    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    color: $primary-color-100;

    text-transform: uppercase;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;

        width: 6px;
        height: 6px;

        border: 2px solid $primary-color-50;
        border-top: 0;
        border-left: 0;
        transform: translateY(-50%) rotate(45deg);
    }

    &--expanded::after {
        transform: rotate(-135deg);
    }
}

.c-checkbox-expandable__list {
    padding: 0;
    margin: 0;

    list-style-type: none;
}